import {takeLatest , call, put , all, fork,takeEvery} from 'redux-saga/effects';
import axios from 'axios';
import actions from '../actions';

export function* fetchRestaurantByNameSaga() {
    yield takeEvery(actions.FETCH_RESTAURANT_BY_NAME,function* (actionObj) {
        let name = actionObj.payload;
        const fetchRestaurantByNameRequest=()=>axios({
           method: 'get',
           url:`http://ec2-13-234-225-152.ap-south-1.compute.amazonaws.com/api/pwa/restaurant/byName/${name}`,
           headers : {
               'Content-Type' : 'application/json'
           }
        });


        try {
            const response = yield call(fetchRestaurantByNameRequest);
            if(response.status<300)
            {
                yield put({
                    type : actions.FETCH_RESTAURANT_BY_NAME_SUCCESS,
                    payload : response.data,
                })
            }
            else
            {
                yield put({
                    type : actions.FETCH_RESTAURANT_BY_NAME_FAILURE
                })
            }
        }catch(Exception){
            yield put({
                type : actions.FETCH_RESTAURANT_BY_NAME_FAILURE
            })
        }

    })
}




export default function* rootSaga() {
    yield all(
        [
            fork(fetchRestaurantByNameSaga),
        ]
    )
}