import React from 'react';
import {Link} from 'react-router-dom';
import {withStyles} from '@material-ui/core/styles'
import PropTypes from 'prop-types';
import classnames from 'classnames';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {RateReview, Star, StarBorder} from '@material-ui/icons';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';


const styles=theme=>({
    card : {
        maxWidth:'100%',
    },
    cardName:{
        fontSize:'0.9rem',
    },
    cardAddress:{
        fontSize:'0.63rem'
    },
    cardMedia:{
        width:'100%',
        height:'300px',
        // height:'auto'
    },
    cardDescription:{

    },
    cardActions:{
        display:'flex'
    },
    // goodColor:{
    //     color:'#006400'
    // },
    // badColor:{
    //     color:'#800000'
    // },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    specialityCard:{
        width:'40%',
        height:'96%',
        marginLeft:'2%',
        marginRight:'2%'
    },
    specialityCardMedia:{
        objectFit:'cover',
    },
    specialityCardContent:{
      padding:'0px'
    },
    specialityCardTypography:{
       marginLeft:'auto',
       marginRight:'auto',
        fontSize:'0.85em',
        textAlign:'center',
        fontFamily:'inherit',
        marginTop:'3px'
    },
});
class RestaurantCard extends React.Component{
    state={
        expanded:false
    };

    handleExpandClick = ()=>{
      this.setState({expanded:!this.state.expanded});
    };

    createStars=(rating)=>{
      let children=[];
      let comp = Math.floor(rating);
      for(let i = 1;i<=5;i++)
      {
         children.push(
             i<=comp ? <Star style={{fill : '#f7c41b' , fontSize : '1.1rem'}} key={i}/> : <StarBorder style={{fill : '#f7c41b',fontSize:'1.1rem'}} key={i}/>
         )
      }
      return children;
    };

    createSpecialityCards=(data,classes)=>{
      let children=[];
      data.map((ele,index)=>{
          children.push(
              <Card key={index} className={classes.specialityCard}>
                  <CardMedia
                      component="img"
                      alt={ele.name}
                      title={ele.name}
                      image={ele.image}
                      height="100"
                      className={classes.specialityCardMedia}/>
                  <CardContent component="div" className={classes.specialityCardContent}>
                      <Typography  className={classes.specialityCardTypography}>{ele.name}</Typography>
                      <Typography  gutterBottom className={classes.specialityCardTypography}>{ele.price} &#8377;</Typography>
                  </CardContent>
              </Card>
          )
      });

        // for(let i=0;i<5;i++)
        // {
        //     children.push(
        //         <Card key={i} className={classes.specialityCard}>
        //             <CardMedia
        //                 component="img"
        //                 alt={data[0].name}
        //                 title={data[0].name}
        //                 image={data[0].image}
        //                 height="100"
        //                 className={classes.specialityCardMedia}/>
        //             <CardContent component="div" className={classes.specialityCardContent}>
        //                 <Typography  className={classes.specialityCardTypography}>{data[0].name}</Typography>
        //                 <Typography  gutterBottom className={classes.specialityCardTypography}>{data[0].price} &#8377;</Typography>
        //             </CardContent>
        //         </Card>
        //     )
        // }


      return children;
    };

  render()
  {
      const {classes} = this.props;
      const cardData = this.props;
      console.log("cardData = ",cardData);

      return(
          <Card className={classes.card}>
              <CardHeader
                  title={<span style={{display:'flex'}}>
                      <Typography className={classes.cardName}><Link to={{
                          pathname:`/restaurant/`+cardData.name.split(" ").join("-"),
                          state:{restaurantId:cardData._id}
                      }}
                      style={{textDecoration:'none',color:'black'}}>
                          {cardData.name}</Link>
                      </Typography>
                      <Typography className={classes.cardName} style={{marginLeft:'auto'}}>
                          {
                              this.createStars(cardData.rating)
                          }
                      </Typography>
                  </span>}
                  subheader={<Typography className={classes.cardAddress}>{cardData.address}</Typography>}
                  disableTypography={true}
              />
            <CardMedia className={classes.cardMedia} image={cardData.image[0]} title={cardData.name}/>
              <CardContent>
                  <Typography component="p" className={classes.cardDescription}>
                      {cardData.description}
                  </Typography>
              </CardContent>
              <CardActions className={classes.cardActions}>
                  <IconButton aria-label="Add to favorites">
                      <FavoriteIcon />
                  </IconButton>
                  <IconButton aria-label="Share">
                      <ShareIcon />
                  </IconButton>
                  <Link to={{pathname : `/restaurant/${cardData.name.split(" ").join("-")}/review` , state : {id : cardData._id}}}>
                  <IconButton aria-label="Write Review">
                      <RateReview />
                  </IconButton>
                  </Link>

                  <IconButton
                      className={classnames(classes.expand, {
                          [classes.expandOpen]: this.state.expanded,
                      })}
                      onClick={this.handleExpandClick}
                      aria-expanded={this.state.expanded}
                      aria-label="Show more"
                  >
                      <ExpandMoreIcon />
                  </IconButton>
              </CardActions>
              <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
                 <CardContent>

                     {
                         (cardData.speciality!==undefined&&cardData.speciality.length!==0)
                         &&(<Typography variant="subtitle2" gutterBottom>
                             Specialities
                         </Typography>)
                     }

                     {
                         (cardData.speciality!==undefined&&cardData.speciality.length!==0)&&(
                             <div style={{whiteSpace:'nowrap',
                                 width : '100%',
                                 overflowX:'scroll',
                                 height:'160px',
                                 display:'-webkit-box'
                             }}>
                                 {this.createSpecialityCards(cardData.speciality,classes)}
                             </div>
                         )
                     }



                 </CardContent>
              </Collapse>
          </Card>
      );
  }
};

RestaurantCard.propTypes={
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(RestaurantCard);

