import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import {connect} from 'react-redux';
import PropTypes from 'prop-types'
import {
    Grid,
    IconButton,
    Stepper,
    Step,
    StepLabel,
    Button, CircularProgress , TextField , Typography , Select , Input , MenuItem
} from '@material-ui/core';
import {Link} from "react-router-dom";
import {ArrowBack, Star, StarBorder} from "@material-ui/icons";
import {bindActionCreators} from "redux";
import axios from 'axios';


const styles=(theme)=>({
    root:{
      maxWidth:'100vw',
      maxHeight:'100vh'
    },
    parent:{
        maxWidth:'100%',
        height:'100%',
        padding:'3%'
    },
    loadingChild:{
        width:'100%',
        height:'100%',
        backgroundColor:'#fff0f5',
        opacity:'0.6',
        zIndex:100,
        position:'fixed'
    },
    progress:{
        marginTop:'50%',
        marginLeft:'40%',
        opacity:'1',
    },
    container:{
        width:'100%',
        height:'auto',
    },
    changingDiv:{
      height:'100%',
      maxWidth:'100%',
        marginTop:'7%',
        textAlign:'center'
    },
    bottomContainer:{
      width:'100%',
      padding:0,
        zIndex:'100',
        bottom:0,
        position:'fixed',
        display:'flex'
    },
    stepperButtons:{
      width:'100%',
      textAlign: 'center',
        color:'white',
        backgroundColor:'green',
        borderRadius:0,
        borderColor:'black',
        '&:hover':{
            backgroundColor:'green'
        },
    },
});

class Review extends React.Component{
    state={
        activeStep:0,
        file:null,
        fileSrc:"",
        loading:false,
        starNumberTouched : 0,
        dishes : [],
        likedDishes : [],
        dislikedDishes : [],
    };
//     const {fetchDishesByRestaurantId} = this.props;
// fetchDishesByRestaurantId(this.state.id);
    constructor(props){
        super(props);
        this.state.id = props.location.state.id;
    }

    steps=['Upload Bill','Write Review','Tell us what you like'];

    /*****------EVENT HANDLERS---------------*****/
    handleImageChange=(event)=>{
        let file = document.getElementById('raised-button-file').files[0];
        let reader = new FileReader();
        if(file!=null){
            reader.readAsDataURL(file);
            this.setState({loading:true});
            reader.addEventListener("load",()=>{
                this.setState({loading:false,file:file,fileSrc:reader.result});
            },false)
        }
        if(file==null)
        {
            document.getElementById("image-component").src="";
            this.setState({fileSrc:"",file:null,loading:false});
        }
    };

    previousStep=()=>{
        let val = this.state.activeStep;
        if(val-1===2)
        {
            this.setState({activeStep:val-1});
            this.fetchDishes();
        }
        else
            this.setState({activeStep:val-1});
    };
    nextStep=()=>{
        let val = this.state.activeStep;
        if(val+1===2)
        {
            this.setState({activeStep:val+1});
            this.fetchDishes();
        }
        else if(val+1===3)
        {
            this.setState({activeStep:val+1});
            this.uploadReview();
        }
        else
            this.setState({activeStep:val+1});
    };
    /******----------------------------------*****/
    keyLineChange=(event)=>{
        this.setState({keyLine : event.target.value});
    };
    reviewChange=(event)=>{
        this.setState({content : event.target.value});
    };
    handleStarTouched=(starNumber)=>{
      this.setState({starNumberTouched : starNumber})
    };

    createStars=()=>{
        let children = [];
        const {starNumberTouched} = this.state;
        for(let i=1;i<=5;i++)
        {
            children.push(
                i<=starNumberTouched ? <Star style={{fill : '#f7c41b' , fontSize : '1.7rem'}} key={i} onClick={()=>this.handleStarTouched(i)}/> : <StarBorder style={{fill : '#f7c41b',fontSize:'1.7rem'}} key={i} onClick={()=>this.handleStarTouched(i)}/>
            )
        }
        return children;
    };
    handleAmount=(event)=>{
      this.setState({amount : event.target.value});
    };

    createOptions=(data)=>{
        let children=[];
        for(let i=0;i<data.length;i++)
        {
            children.push(
                <option value={data._id}>
                    data.name
                </option>
            )
        }
        return children;
    };

    /******-----------API REQUESTS-------------------*****/
    fetchDishes = ()=>{
        this.setState({loading:true});
      axios({
            method : 'get',
            url : `http://ec2-13-234-225-152.ap-south-1.compute.amazonaws.com/api/pwa/restaurant/onlyDishes/${this.state.id}`,
            headers : {
                'Content-Type' : 'application/json'
            }
      }).then((response)=>{
          this.setState({loading:false,dishes:response.data});
      }).catch((error)=>{
          console.log("error = ",error);
          alert("error in fetching");
      });
    };
    uploadReview=()=>{
      console.log("chal rha h");
    };
    /*****-----------------------------------*****/

    returnComponent=(classes)=>{
      switch (this.state.activeStep) {
          case 0 : return(
            <div className={classes.changingDiv}>
                <img src={this.state.fileSrc===""?"":this.state.fileSrc} id="image-component" style={{width:'70%',height:'auto'}}/>
                <div style={{clear:'both'}}/>
                    <div style={{marginTop:'7%',marginBottom:'15%'}}>
                        <input
                            accept="image/*"
                            className={classes.input}
                            style={{ display: 'none' }}
                            id="raised-button-file"
                            type="file"
                            onChange={this.handleImageChange}
                        />
                        <label htmlFor="raised-button-file">
                    <Button variant="contained" component="span" className={classes.button} size="small">
                        {this.state.file ? "Upload new Image" : "Upload Bill" }
                    </Button>
                    </label>
                        </div>
            </div>
          );
          case 1 : return(
              <div className={classes.changingDiv}>
                  <Grid container={true} spacing={0} style={{
                      maxWidth:'100%',
                      paddingRight : '10%',
                      paddingLeft : '10%',
                  }}>
                      <Grid item={true} xs={12} sm={12} style={{textAlign:'center'}}>
                          {this.createStars()}
                      </Grid>
                      <Grid item={true} xs={12} sm={12}>
                          <TextField autoFocus={true} fullWidth={true} placeholder="please Describe your Experience in one line." value={this.state.keyLine} onChange={this.state.keyLineChange}/>
                      </Grid>
                      <Grid item={true} xs={12} sm={12}>
                            <TextField multiline rows={3} fullWidth={true} placeholder="Enter your review here in detail" value={this.state.content} onChange={this.state.reviewChange}/>
                      </Grid>
                  </Grid>
              </div>
          );
          case 2 :
              return(
              <div className={classes.changingDiv}>
                  <Grid container={true} spacing={0} style={{
                      maxWidth:'100%',
                      paddingRight : '10%',
                      paddingLeft : '10%',
                  }}>
                      <Grid item={true} xs={12} sm={12}>
                        <Typography>
                            Dishes that you liked
                        </Typography>
                          <Select
                              multiple={true} value={this.state.likedDishes}
                                onChange={this.handleLikedDishesChange}
                              input={<Input id="select-multiple" />}
                          >
                              {
                                  (this.state.dishes.length>0)&&(this.state.dishes.map((obj,index)=>(
                                      <option key={index} value={obj._id}>
                                          {obj.name}
                                      </option>
                                  )))
                              }
                          </Select>
                      </Grid>
                      <Grid item={true} xs={12} sm={12}>
                          <Typography>
                              Dishes that you disliked
                          </Typography>
                          <Select
                          multiple={true} value={this.state.dislikedDishes}
                          onChange={this.handleLikedDishesChange}
                          input={<Input id="select-multiple" />}
                          MenuProps={{
                              maxHeight : '5%',
                              width : '50%'
                          }}>
                              {
                                  (this.state.dishes.length>0)&&(this.state.dishes.map((obj,index)=>(
                                      <MenuItem key={index} value={obj._id}>
                                          {obj.name}
                                      </MenuItem>
                                  )))
                              }
                          </Select>
                      </Grid>
                      <Grid item={true} xs={12} sm={12} style={{textAlign:'center'}}>
                          <TextField placeholder="Enter Bill Amount" value={this.state.amount} onChange={this.state.handleAmount}/>
                      </Grid>
                  </Grid>
              </div>
          );
          case 3 : return(
              <div className={classes.changingDiv}>
                  <p>Thank you for your review</p>
                  <p> We will process your bill and then notify you </p>
              </div>
          );
          default : return(
              <div>ho gya</div>
          );
      }
    };

    render()
    {
        const {classes} = this.props;
        return(
            <div className={classes.root}>
                {
                    (this.state.loading)&&(
                        <div className={classes.loadingChild}>
                            <CircularProgress  color="secondary" size={60} className={classes.progress}/>
                        </div>
                    )
                }
            <div className={classes.parent}>
              <Grid container spacing={0} style={{marginTop:'3%'}}>
                  <Grid item xs={12}>
                      <Link to="/"><IconButton style={{paddingLeft:'0px'}}>
                          <ArrowBack/>
                      </IconButton></Link>
                  </Grid>
              </Grid>
                <Stepper activeStep={this.state.activeStep} alternativeLabel>
                    {
                        this.steps.map((label,index)=>(
                            <Step key={index}>
                                <StepLabel>{label}</StepLabel>
                            </Step>
                        ))
                    }
                </Stepper>
                <div className={classes.container}>
                    {this.returnComponent(classes)}
                </div>
            </div>
                <div className={classes.bottomContainer}>
                    <div style={{width:'50%'}}><Button variant="contained"
                                                       disabled={this.state.activeStep<1}
                                                       component="span" className={classes.stepperButtons} size="medium" onClick={()=>this.previousStep()}>
                        Back
                    </Button></div>
                    <div style={{width:'50%'}}><Button variant="contained" component="span" className={classes.stepperButtons} size="medium"
                                                       onClick={()=>{
                                                           this.state.activeStep<3 ? this.nextStep() : this.uploadReview()
                                                       }}
                    >
                        {this.state.activeStep<3 ? "Next" : "Done"}
                    </Button></div>
                </div>
            </div>
        )
    }
}
Review.propTypes={
    classes: PropTypes.object.isRequired,
};

function mapStateToProps(state) {
    return {
        ...state
    }
}
function mapDispatchToProps(dispatch){
    return bindActionCreators({

    },dispatch);
}

export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(Review));
