import {all} from 'redux-saga/effects';
import DishSagas from './dishSaga';
import ReviewSagas from './reviewSaga';
import RestaurantSagas from './restaurantSaga';

export default function* rootSaga(getState) {
    yield all([
        DishSagas(),
        ReviewSagas(),
        RestaurantSagas(),
    ])
}